.profile__avatar {
  width: 120px;
  height: 120px;
  background-image: url('../../../images/pen.svg');
  display: flex;
  margin: 0 30px 0 0;
  justify-content: center;
  align-items: center;
  background-repeat: no-repeat;
  background-position: center;
}

@media screen and (max-width: 900px) {
  .profile__avatar {
    margin: 0 0 20px 0;
  }
}
