.popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.4s, opacity 0.4s;
  justify-content: center;
  align-items: center;
}

@media screen and (max-width: 320px) {
  .popup {
    width: 320px;
  }
}
